import React, { useState, useEffect, useCallback } from 'react';
import { Search, X, Loader, ChevronDown, ChevronRight, ChevronLeft, ChevronUp } from 'lucide-react';
import { extractChatData } from './utils';

/**
 * 全新的增强型全局搜索面板，与SearchManager集成
 */
const SearchPanel = ({
  isOpen,
  onClose,
  files,
  currentFileIndex,
  processedData,
  searchQuery,
  setSearchQuery,
  jumpToMessage,
  setContentSearchQuery,
  handleSelectFile
}) => {
  const [results, setResults] = useState([]);
  const [isSearching, setIsSearching] = useState(false);
  const [statusMessage, setStatusMessage] = useState('');
  const [currentResultIndex, setCurrentResultIndex] = useState(-1);
  const [expandedFiles, setExpandedFiles] = useState({});
  const [flattenedResults, setFlattenedResults] = useState([]);
  
  // 监听搜索查询变化，自动执行搜索
  useEffect(() => {
    if (isOpen && searchQuery.trim()) {
      performSearch();
    }
  }, [isOpen, searchQuery]);
  
  // 监听全局搜索状态更新
  useEffect(() => {
    const handleSearchUpdate = (e) => {
      const { results, activeIndex, totalCount } = e.detail;
      
      if (totalCount > 0 && activeIndex >= 0 && activeIndex < totalCount) {
        // 更新当前选择的结果
        setCurrentResultIndex(activeIndex);
        
        // 确保对应的文件是展开的
        const result = results[activeIndex];
        if (result) {
          setExpandedFiles(prev => ({
            ...prev,
            [result.fileIndex]: true
          }));
        }
      }
    };
    
    window.addEventListener('search-status-update', handleSearchUpdate);
    return () => window.removeEventListener('search-status-update', handleSearchUpdate);
  }, []);
  
  // 重置组件状态
  const resetState = () => {
    setResults([]);
    setCurrentResultIndex(-1);
    setStatusMessage('');
    setExpandedFiles({});
    setFlattenedResults([]);
  };
  
  // 当面板打开/关闭时重置状态
  useEffect(() => {
    if (!isOpen) {
      resetState();
    } else if (searchQuery.trim()) {
      performSearch();
    }
  }, [isOpen]);
  
  // 获取所有搜索结果（扁平化）
  const getAllResults = useCallback(() => {
    let allResults = [];
    results.forEach(fileResult => {
      fileResult.matches.forEach(match => {
        allResults.push({
          fileIndex: fileResult.fileIndex,
          messageIndex: match.messageIndex,
          fileName: fileResult.fileName,
          preview: match.preview
        });
      });
    });
    return allResults;
  }, [results]);
  
  // 跳转到特定结果
  const goToResult = useCallback((index) => {
    if (!window.searchManager) return;
    
    // 使用SearchManager跳转到结果
    window.searchManager.goToResult(index);
    
    // 选择结果后关闭搜索面板
    setTimeout(() => {
      onClose();
    }, 100);
  }, [onClose]);
  
  // 上一个结果
  const goToPrevResult = useCallback(() => {
    if (!window.searchManager) return;
    window.searchManager.prevResult();
    
    // 不关闭面板，让用户可以继续浏览结果
  }, []);
  
  // 下一个结果
  const goToNextResult = useCallback(() => {
    if (!window.searchManager) return;
    window.searchManager.nextResult();
    
    // 不关闭面板，让用户可以继续浏览结果
  }, []);
  
  // 执行搜索
  const performSearch = async () => {
    if (!searchQuery.trim()) {
      resetState();
      return;
    }
    
    setIsSearching(true);
    setStatusMessage('正在搜索...');
    
    const query = searchQuery.toLowerCase();
    const searchResults = [];
    let totalMatches = 0;
    
    try {
      // 单文件与多文件逻辑统一
      if (files.length === 1) {
        // 单文件搜索 - 使用当前已加载的处理数据
        if (processedData && processedData.chat_history) {
          const matches = processedData.chat_history.filter(msg => 
            msg.display_text.toLowerCase().includes(query)
          );
          
          if (matches.length > 0) {
            searchResults.push({
              fileIndex: 0,
              fileName: files[0].name,
              title: processedData.meta_info.title || 'Untitled',
              matchCount: matches.length,
              matches: matches.map(msg => ({
                messageIndex: msg.index,
                preview: msg.display_text.substring(0, 100),
                sender: msg.sender_label,
                highlightedPreview: highlightTextWithQuery(msg.display_text.substring(0, 100), query)
              }))
            });
            totalMatches += matches.length;
          }
        }
      } else {
        // 多文件搜索
        for (let i = 0; i < files.length; i++) {
          const file = files[i];
          
          // 如果当前索引是当前文件索引，则使用已经处理的数据
          if (i === currentFileIndex && processedData) {
            const matches = processedData.chat_history.filter(msg => 
              msg.display_text.toLowerCase().includes(query)
            );
            
            if (matches.length > 0) {
              searchResults.push({
                fileIndex: i,
                fileName: file.name,
                title: processedData.meta_info.title || 'Untitled',
                matchCount: matches.length,
                matches: matches.map(msg => ({
                  messageIndex: msg.index,
                  preview: msg.display_text.substring(0, 100),
                  sender: msg.sender_label,
                  highlightedPreview: highlightTextWithQuery(msg.display_text.substring(0, 100), query)
                }))
              });
              totalMatches += matches.length;
            }
          } else {
            // 需要读取和处理此文件
            try {
              const text = await file.text();
              const jsonData = JSON.parse(text);
              const data = extractChatData(jsonData);
              
              const matches = data.chat_history.filter(msg => 
                msg.display_text.toLowerCase().includes(query)
              );
              
              if (matches.length > 0) {
                searchResults.push({
                  fileIndex: i,
                  fileName: file.name,
                  title: data.meta_info.title || 'Untitled',
                  matchCount: matches.length,
                  matches: matches.map(msg => ({
                    messageIndex: msg.index,
                    preview: msg.display_text.substring(0, 100),
                    sender: msg.sender_label,
                    highlightedPreview: highlightTextWithQuery(msg.display_text.substring(0, 100), query)
                  }))
                });
                totalMatches += matches.length;
              }
            } catch (error) {
              console.error(`搜索文件 ${file.name} 出错:`, error);
            }
          }
        }
      }
      
      setResults(searchResults);
      
      if (searchResults.length === 0) {
        setStatusMessage(`未找到包含 "${searchQuery}" 的消息`);
      } else {
        setStatusMessage(`在 ${searchResults.length} 个文件中找到 ${totalMatches} 条匹配消息`);
        
        // 自动选择第一个结果
        setCurrentResultIndex(0);
        
        // 展开包含结果的文件
        const expandedState = {};
        searchResults.forEach(result => {
          expandedState[result.fileIndex] = true;
        });
        setExpandedFiles(expandedState);
        
        // 生成扁平化结果列表
        const flat = getAllResults();
        setFlattenedResults(flat);
        
        // 通知搜索管理器更新结果
        if (window.searchManager) {
          // 转换为标准格式
          const standardResults = flat.map(item => ({
            type: item.fileIndex === currentFileIndex ? 'local' : 'cross-file',
            fileIndex: item.fileIndex,
            messageIndex: item.messageIndex,
            preview: item.preview
          }));
          
          // 执行新搜索
          window.searchManager.search(searchQuery);
        }
      }
    } catch (error) {
      console.error("搜索出错:", error);
      setStatusMessage(`搜索失败: ${error.message}`);
    } finally {
      setIsSearching(false);
    }
  };
  
  // 高亮查询文本
  const highlightTextWithQuery = (text, query) => {
    if (!text || !query) return text;
    
    const lowerText = text.toLowerCase();
    const lowerQuery = query.toLowerCase();
    
    const parts = [];
    let lastIndex = 0;
    let index = lowerText.indexOf(lowerQuery);
    
    while (index !== -1) {
      // 添加查询词前面的文本
      if (index > lastIndex) {
        parts.push({
          text: text.substring(lastIndex, index),
          highlight: false
        });
      }
      
      // 添加高亮的查询词
      parts.push({
        text: text.substring(index, index + lowerQuery.length),
        highlight: true
      });
      
      lastIndex = index + lowerQuery.length;
      index = lowerText.indexOf(lowerQuery, lastIndex);
    }
    
    // 添加剩余文本
    if (lastIndex < text.length) {
      parts.push({
        text: text.substring(lastIndex),
        highlight: false
      });
    }
    
    return parts;
  };
  
  // 切换文件展开/折叠
  const toggleFileExpanded = (fileIndex) => {
    setExpandedFiles(prev => ({
      ...prev,
      [fileIndex]: !prev[fileIndex]
    }));
  };
  
  // 清除搜索
  const clearSearch = () => {
    setSearchQuery('');
    resetState();
  };
  
  // 点击单个搜索结果
  const handleResultClick = (fileIndex, messageIndex, allResultsIndex) => {
    if (window.searchManager) {
      // 使用全局搜索管理器
      window.searchManager.goToResult(allResultsIndex);
    } else {
      // 备用方案：直接跳转
      jumpToMessage(fileIndex, messageIndex);
    }
    
    // 关闭搜索面板
    onClose();
  };

  // 键盘导航处理
  useEffect(() => {
    const handleKeyDown = (e) => {
      if (!isOpen) return;
      
      // 如果事件是在输入框上触发的，只处理Escape键
      if (e.target.tagName === 'INPUT' && e.key !== 'Escape') return;
      
      if (e.key === 'Escape') {
        onClose();
      } else if (e.key === 'Enter') {
        e.preventDefault();
        if (e.shiftKey) {
          goToPrevResult(); // Shift+Enter 上一个结果
        } else {
          goToNextResult(); // Enter 下一个结果
        }
      }
    };
    
    window.addEventListener('keydown', handleKeyDown);
    return () => window.removeEventListener('keydown', handleKeyDown);
  }, [isOpen, goToPrevResult, goToNextResult, onClose]);
  
  // 为搜索输入框单独添加键盘处理
  const handleInputKeyDown = (e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      if (e.shiftKey) {
        goToPrevResult(); // Shift+Enter 上一个结果
      } else {
        goToNextResult(); // Enter 下一个结果
      }
    }
  };
  
  // 如果面板未打开，不渲染任何内容
  if (!isOpen) return null;
  
  // 获取所有结果以便索引计算
  const allResults = flattenedResults;
  const totalResultCount = allResults.length;
  
  return (
    <div className="fixed top-10 left-20 right-20 bg-white rounded-lg shadow-xl border border-gray-300 z-50 max-h-[80vh] overflow-auto">
      {/* 标题栏 */}
      <div className="p-3 border-b border-gray-300 bg-blue-50 sticky top-0 z-10">
        <div className="flex justify-between items-center">
          <div className="flex-1">
            <h3 className="font-bold text-blue-800 flex items-center">
              <Search className="h-5 w-5 mr-2 text-blue-600" />
              全局搜索
            </h3>
          </div>
          
          {/* 搜索控件 */}
          <div className="flex-1 mx-4">
            <div className="relative">
              <input 
                type="text" 
                className="w-full px-10 py-2 border border-blue-300 rounded focus:ring-2 focus:ring-blue-300 focus:border-blue-500 transition-all"
                placeholder="搜索所有对话..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
                onKeyDown={handleInputKeyDown}
                autoFocus
              />
              <Search className="absolute left-3 top-2.5 h-5 w-5 text-gray-400" />
              
              {searchQuery && (
                <button
                  className="absolute right-3 top-2.5 text-gray-400 hover:text-gray-600 focus:outline-none"
                  onClick={clearSearch}
                  title="清除搜索"
                >
                  <X className="h-5 w-5" />
                </button>
              )}
            </div>
          </div>
          
          {/* 导航和关闭按钮 */}
          <div className="flex items-center space-x-2">
            {totalResultCount > 0 && (
              <div className="flex items-center space-x-1">
                <button
                  className="p-1.5 bg-gray-100 hover:bg-gray-200 rounded focus:outline-none focus:ring-2 focus:ring-blue-300 transition-colors"
                  onClick={goToPrevResult}
                  title="上一个结果"
                  disabled={isSearching}
                >
                  <ChevronUp className="h-4 w-4" />
                </button>
                
                <div className="text-sm font-medium px-2">
                  {currentResultIndex + 1}/{totalResultCount}
                </div>
                
                <button
                  className="p-1.5 bg-gray-100 hover:bg-gray-200 rounded focus:outline-none focus:ring-2 focus:ring-blue-300 transition-colors"
                  onClick={goToNextResult}
                  title="下一个结果"
                  disabled={isSearching}
                >
                  <ChevronDown className="h-4 w-4" />
                </button>
              </div>
            )}
            
            <button 
              className="ml-3 bg-gray-200 hover:bg-gray-300 px-3 py-1.5 rounded focus:outline-none focus:ring-2 focus:ring-gray-400 transition-colors"
              onClick={onClose}
            >
              关闭
            </button>
          </div>
        </div>
      </div>
      
      {/* 搜索状态 */}
      <div className="px-3 py-2 bg-gray-50 border-b border-gray-200 sticky top-16 z-[5]">
        <div className="flex items-center justify-between">
          <div className="flex items-center">
            {isSearching ? (
              <div className="flex items-center text-blue-600">
                <Loader className="animate-spin h-4 w-4 mr-2" />
                <span>正在搜索...</span>
              </div>
            ) : (
              <div>{statusMessage}</div>
            )}
          </div>
          
          {totalResultCount > 0 && (
            <div className="text-sm text-gray-600">
              {currentResultIndex >= 0 && <span>当前: 第 {currentResultIndex + 1} 个结果</span>}
              <span className="ml-2 text-xs text-gray-500">(上/下箭头: 导航结果)</span>
            </div>
          )}
        </div>
      </div>
      
      {/* 搜索结果列表 */}
      <div className="p-3">
        {results.length === 0 ? (
          <div className="text-center py-4 text-gray-500">
            {isSearching ? (
              <div className="flex flex-col items-center">
                <Loader className="animate-spin h-8 w-8 mb-2 text-blue-500" />
                <span>正在搜索所有对话...</span>
              </div>
            ) : (
              searchQuery.trim() ? "没有找到匹配的结果" : "请输入搜索关键词"
            )}
          </div>
        ) : (
          <div>
            {results.map((fileResult, fileIdx) => {
              const isExpanded = expandedFiles[fileResult.fileIndex] || false;
              
              // 计算此文件中第一个结果的全局索引
              let globalStartIndex = 0;
              for (let i = 0; i < fileIdx; i++) {
                globalStartIndex += results[i].matches.length;
              }
              
              return (
                <div key={fileIdx} className="mb-4 border border-gray-200 rounded-lg overflow-hidden">
                  {/* 文件标题 */}
                  <div 
                    className={`p-3 flex justify-between items-center cursor-pointer ${
                      fileResult.fileIndex === currentFileIndex 
                        ? 'bg-blue-100 hover:bg-blue-200' 
                        : 'bg-gray-50 hover:bg-gray-100'
                    }`}
                    onClick={() => toggleFileExpanded(fileResult.fileIndex)}
                  >
                    <div className="font-medium text-blue-700 flex items-center">
                      {isExpanded ? (
                        <ChevronDown className="h-4 w-4 mr-1" />
                      ) : (
                        <ChevronRight className="h-4 w-4 mr-1" />
                      )}
                      <span>{fileResult.title || fileResult.fileName}</span>
                      
                      {fileResult.fileIndex === currentFileIndex && (
                        <span className="ml-2 text-xs bg-blue-600 text-white px-2 py-0.5 rounded-full">
                          当前文件
                        </span>
                      )}
                    </div>
                    
                    <div className="text-sm bg-blue-100 text-blue-800 px-2 py-0.5 rounded-full">
                      {fileResult.matchCount}个结果
                    </div>
                  </div>
                  
                  {/* 匹配项列表 - 仅在展开时显示 */}
                  {isExpanded && (
                    <div className="border-t border-gray-200">
                      {fileResult.matches.map((match, matchIdx) => {
                        // 计算此结果的全局索引
                        const globalIndex = globalStartIndex + matchIdx;
                        const isActive = currentResultIndex === globalIndex;
                        
                        return (
                          <div 
                            key={matchIdx}
                            className={`py-2 px-3 hover:bg-blue-50 cursor-pointer border-b border-gray-100 ${
                              isActive ? 'bg-blue-100' : ''
                            }`}
                            onClick={() => handleResultClick(fileResult.fileIndex, match.messageIndex, globalIndex)}
                          >
                            <div className="text-xs text-gray-500 mb-1 flex justify-between">
                              <span>{match.sender} | 消息 #{match.messageIndex + 1}</span>
                              {isActive && (
                                <span className="text-blue-600 font-medium">当前选中</span>
                              )}
                            </div>
                            <div className="text-sm">
                              {Array.isArray(match.highlightedPreview) ? (
                                match.highlightedPreview.map((part, i) => (
                                  <span 
                                    key={i} 
                                    className={part.highlight ? 'bg-yellow-200 font-medium' : ''}
                                  >
                                    {part.text}
                                  </span>
                                ))
                              ) : (
                                match.preview
                              )}
                              ...
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  )}
                </div>
              );
            })}
          </div>
        )}
      </div>
    </div>
  );
};

export default SearchPanel;