import React, { useState, useEffect, useRef, forwardRef, useImperativeHandle } from 'react';
import { Clipboard, Code, Search, GitBranch, FileText, Star, AlertTriangle, Book, Bot, User, Layers, MessageSquare, Wrench } from 'lucide-react';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { tomorrow } from 'react-syntax-highlighter/dist/esm/styles/prism';

// MessageDetail 组件 - 使用 forwardRef 以便可以从父组件访问
const MessageDetail = forwardRef(({ 
  processedData, 
  selectedMessageIndices,
  activeTab,
  setActiveTab,
  searchQuery,  // 全局搜索查询
  contentSearchQuery, // 新增: 内容搜索关键词
  files,
  currentFileIndex,
  handleSelectFile
}, ref) => {
  const [message, setMessage] = useState(null);
  const [artifacts, setArtifacts] = useState([]);
  const [tools, setTools] = useState([]);
  const [citations, setCitations] = useState([]);
  const [showThinking, setShowThinking] = useState(false);
  const messageContentRef = useRef(null);
  
  // 内容搜索状态
  const [contentMatches, setContentMatches] = useState([]);
  const [currentMatchIndex, setCurrentMatchIndex] = useState(-1);
  
  // 暴露给父组件的方法
  useImperativeHandle(ref, () => ({
    searchContent: (query) => {
      findContentMatches(query);
    },
    clearContentSearch: () => {
      removeContentHighlights();
      setContentMatches([]);
      setCurrentMatchIndex(-1);
    }
  }));
  
  // 监听内容搜索事件
  useEffect(() => {
    const handleContentSearch = (e) => {
      const { query } = e.detail;
      
      if (!query || !messageContentRef.current) return;
      
      // 查找匹配项
      findContentMatches(query);
    };
    
    window.addEventListener('content-search', handleContentSearch);
    return () => window.removeEventListener('content-search', handleContentSearch);
  }, []);
  
  // 监听内容搜索导航事件
  useEffect(() => {
    const handleContentSearchNavigation = (e) => {
      const { action } = e.detail;
      
      if (!contentMatches.length) return;
      
      if (action === 'prev') {
        navigateToPrevMatch();
      } else if (action === 'next') {
        navigateToNextMatch();
      }
    };
    
    window.addEventListener('content-search-navigation', handleContentSearchNavigation);
    return () => window.removeEventListener('content-search-navigation', handleContentSearchNavigation);
  }, [contentMatches]);
  
  // 当contentSearchQuery变化时执行搜索
  useEffect(() => {
    if (contentSearchQuery && messageContentRef.current) {
      // 延迟执行，确保内容已渲染
      setTimeout(() => {
        findContentMatches(contentSearchQuery);
      }, 100);
    } else if (!contentSearchQuery) {
      removeContentHighlights();
      setContentMatches([]);
      setCurrentMatchIndex(-1);
    }
  }, [contentSearchQuery, message]);
  
  // 查找内容匹配项
  const findContentMatches = (query) => {
    if (!query || !messageContentRef.current || !message) {
      // 通知没有匹配项
      window.dispatchEvent(new CustomEvent('content-search-update', {
        detail: { count: 0, position: 0 }
      }));
      return;
    }
    
    // 清除现有高亮
    removeContentHighlights();
    
    // 在内容中查找匹配项
    const contentElement = messageContentRef.current;
    const text = contentElement.textContent;
    
    if (!text) {
      // 通知没有匹配项
      window.dispatchEvent(new CustomEvent('content-search-update', {
        detail: { count: 0, position: 0 }
      }));
      return;
    }
    
    // 查找所有匹配位置
    const matches = [];
    const walker = document.createTreeWalker(contentElement, NodeFilter.SHOW_TEXT);
    
    let node;
    while (node = walker.nextNode()) {
      const nodeText = node.nodeValue;
      let position = -1;
      const lowerNodeText = nodeText.toLowerCase();
      const lowerQuery = query.toLowerCase();
      
      while ((position = lowerNodeText.indexOf(lowerQuery, position + 1)) !== -1) {
        const range = document.createRange();
        range.setStart(node, position);
        range.setEnd(node, position + query.length);
        matches.push(range);
      }
    }
    
    setContentMatches(matches);
    
    // 如果找到匹配项，高亮第一个
    if (matches.length > 0) {
      setCurrentMatchIndex(0);
      highlightMatch(matches[0], 0);
      
      // 通知匹配数量
      window.dispatchEvent(new CustomEvent('content-search-update', {
        detail: { count: matches.length, position: 0 }
      }));
    } else {
      // 通知没有匹配项
      window.dispatchEvent(new CustomEvent('content-search-update', {
        detail: { count: 0, position: 0 }
      }));
    }
  };
  
  // 高亮特定匹配项
  const highlightMatch = (range, index) => {
    // 移除现有高亮
    removeContentHighlights();
    
    try {
      // 创建高亮元素
      const span = document.createElement('span');
      span.className = index === currentMatchIndex ? 
        'content-highlight current-match' : 'content-highlight';
      
      // 应用高亮
      range.surroundContents(span);
      
      // 滚动到视图
      span.scrollIntoView({ behavior: 'smooth', block: 'center' });
    } catch (error) {
      console.error('高亮匹配项时出错:', error);
    }
  };
  
  // 移除所有内容高亮
  const removeContentHighlights = () => {
    if (!messageContentRef.current) return;
    
    const highlights = messageContentRef.current.querySelectorAll('.content-highlight');
    
    highlights.forEach(highlight => {
      const parent = highlight.parentNode;
      if (parent) {
        // 取出高亮元素的文本内容
        const textNode = document.createTextNode(highlight.textContent);
        // 替换高亮元素
        parent.replaceChild(textNode, highlight);
        // 合并相邻文本节点
        parent.normalize();
      }
    });
  };
  
  // 导航到上一个匹配项
  const navigateToPrevMatch = () => {
    if (!contentMatches.length) return;
    
    const newIndex = currentMatchIndex > 0 ? 
      currentMatchIndex - 1 : contentMatches.length - 1;
    
    setCurrentMatchIndex(newIndex);
    highlightMatch(contentMatches[newIndex], newIndex);
    
    // 通知当前位置
    window.dispatchEvent(new CustomEvent('content-search-update', {
      detail: { 
        count: contentMatches.length,
        position: newIndex
      }
    }));
  };
  
  // 导航到下一个匹配项
  const navigateToNextMatch = () => {
    if (!contentMatches.length) return;
    
    const newIndex = (currentMatchIndex + 1) % contentMatches.length;
    
    setCurrentMatchIndex(newIndex);
    highlightMatch(contentMatches[newIndex], newIndex);
    
    // 通知当前位置
    window.dispatchEvent(new CustomEvent('content-search-update', {
      detail: { 
        count: contentMatches.length,
        position: newIndex
      }
    }));
  };
  
  // 处理选中消息变化
  useEffect(() => {
    if (!processedData || !processedData.chat_history || selectedMessageIndices.length === 0) {
      setMessage(null);
      return;
    }
    
    const selectedIndex = selectedMessageIndices[0]; // 只显示第一个选中的消息详情
    const selectedMessage = processedData.chat_history.find(msg => msg.index === selectedIndex);
    
    if (selectedMessage) {
      setMessage(selectedMessage);
      
      // 提取工件和工具
      setArtifacts(selectedMessage.artifacts || []);
      setTools(selectedMessage.tools || []);
      setCitations(selectedMessage.citations || []);
      
      // 每次加载新消息时，重置内容搜索
      removeContentHighlights();
      setContentMatches([]);
      setCurrentMatchIndex(-1);
      
      // 如果有内容搜索关键词，延迟执行搜索
      if (contentSearchQuery) {
        setTimeout(() => {
          findContentMatches(contentSearchQuery);
        }, 200);
      }
    } else {
      setMessage(null);
    }
  }, [selectedMessageIndices, processedData]);
  
  // 处理全局搜索关键词高亮
  const highlightSearchText = (text) => {
    if (!text || !searchQuery) return text;
    
    // 使用正则表达式拆分文本以进行高亮
    const parts = text.split(new RegExp(`(${searchQuery})`, 'gi'));
    return (
      <>
        {parts.map((part, i) => (
          part.toLowerCase() === searchQuery.toLowerCase() ? (
            <span key={i} className="bg-yellow-200">{part}</span>
          ) : (
            <span key={i}>{part}</span>
          )
        ))}
      </>
    );
  };
  
  // 渲染工具结果内容
  const renderToolResultContent = (content) => {
    if (!content || !Array.isArray(content)) return null;
    
    return content.map((item, idx) => {
      if (typeof item === 'string') {
        return <div key={idx} className="whitespace-pre-wrap mb-2">{item}</div>;
      }
      
      if (typeof item === 'object') {
        // 渲染搜索结果
        if (item.title && item.url) {
          return (
            <div key={idx} className="mb-2">
              <div><a href={item.url} target="_blank" rel="noopener noreferrer" className="text-blue-600 hover:underline">{item.title}</a></div>
              {item.snippet && <div className="text-sm text-gray-600">{item.snippet}</div>}
            </div>
          );
        }
        
        // 渲染其他对象类型
        return (
          <div key={idx} className="mb-2">
            <pre className="bg-gray-100 p-2 rounded overflow-auto text-xs">
              {JSON.stringify(item, null, 2)}
            </pre>
          </div>
        );
      }
      
      return null;
    });
  };
  
  // 渲染代码块
  const renderCode = (code, language = "") => {
    return (
      <div className="relative group">
        <SyntaxHighlighter
          language={language || "javascript"}
          style={tomorrow}
          customStyle={{fontSize: '0.85rem'}}
        >
          {code}
        </SyntaxHighlighter>
        <button 
          className="absolute top-2 right-2 bg-gray-700 text-white p-1 rounded opacity-0 group-hover:opacity-100 transition-opacity"
          onClick={() => {
            navigator.clipboard.writeText(code);
            alert('代码已复制到剪贴板');
          }}
          title="复制代码"
        >
          <Clipboard className="h-4 w-4" />
        </button>
      </div>
    );
  };
  
  // 处理tab切换
  const handleTabChange = (tab) => {
    setActiveTab(tab);
  };
  
  // 空消息处理
  if (!message) {
    return (
      <div className="h-full flex items-center justify-center bg-gray-50 text-gray-500">
        <div className="text-center">
          <div className="mb-4">
            <MessageSquare className="h-12 w-12 mx-auto text-gray-400" />
          </div>
          <p>请选择一条消息查看详情</p>
        </div>
      </div>
    );
  }
  
  return (
    <div className="flex flex-col h-full overflow-hidden">
      {/* 顶部tab导航 */}
      <div className="bg-gray-100 border-b border-gray-300">
        <div className="flex space-x-1 p-1">
          <button 
            className={`px-4 py-2 text-sm font-medium rounded-t-md ${
              activeTab === 'content' ? 'bg-white text-blue-700 border-t border-l border-r border-gray-300' : 'text-gray-600 hover:bg-gray-50'
            }`}
            onClick={() => handleTabChange('content')}
          >
            <div className="flex items-center">
              <MessageSquare className="h-4 w-4 mr-1" />
              内容
            </div>
          </button>
          
          {message.thinking && (
            <button 
              className={`px-4 py-2 text-sm font-medium rounded-t-md ${
                activeTab === 'thinking' ? 'bg-white text-blue-700 border-t border-l border-r border-gray-300' : 'text-gray-600 hover:bg-gray-50'
              }`}
              onClick={() => handleTabChange('thinking')}
            >
              <div className="flex items-center">
                <Bot className="h-4 w-4 mr-1" />
                思考过程
              </div>
            </button>
          )}
          
          {artifacts.length > 0 && (
            <button 
              className={`px-4 py-2 text-sm font-medium rounded-t-md ${
                activeTab === 'artifacts' ? 'bg-white text-blue-700 border-t border-l border-r border-gray-300' : 'text-gray-600 hover:bg-gray-50'
              }`}
              onClick={() => handleTabChange('artifacts')}
            >
              <div className="flex items-center">
                <FileText className="h-4 w-4 mr-1" />
                Artifacts ({artifacts.length})
              </div>
            </button>
          )}
          
          {tools.length > 0 && (
            <button 
              className={`px-4 py-2 text-sm font-medium rounded-t-md ${
                activeTab === 'tools' ? 'bg-white text-blue-700 border-t border-l border-r border-gray-300' : 'text-gray-600 hover:bg-gray-50'
              }`}
              onClick={() => handleTabChange('tools')}
            >
              <div className="flex items-center">
                <Wrench className="h-4 w-4 mr-1" />
                工具 ({tools.length})
              </div>
            </button>
          )}
          
          {citations.length > 0 && (
            <button 
              className={`px-4 py-2 text-sm font-medium rounded-t-md ${
                activeTab === 'citations' ? 'bg-white text-blue-700 border-t border-l border-r border-gray-300' : 'text-gray-600 hover:bg-gray-50'
              }`}
              onClick={() => handleTabChange('citations')}
            >
              <div className="flex items-center">
                <Book className="h-4 w-4 mr-1" />
                引用 ({citations.length})
              </div>
            </button>
          )}
          
          {(message.branch_id || message.branch_level > 0 || message.is_branch_point) && (
            <button 
              className={`px-4 py-2 text-sm font-medium rounded-t-md ${
                activeTab === 'branch' ? 'bg-white text-blue-700 border-t border-l border-r border-gray-300' : 'text-gray-600 hover:bg-gray-50'
              }`}
              onClick={() => handleTabChange('branch')}
            >
              <div className="flex items-center">
                <GitBranch className="h-4 w-4 mr-1" />
                分支信息
              </div>
            </button>
          )}
        </div>
      </div>
      
      {/* 内容区域 */}
      <div className="flex-1 overflow-y-auto bg-white p-4">
        {/* 消息标题与信息 */}
        <div className="mb-4">
          <div className="flex items-center mb-2">
            <div className={`rounded-full w-8 h-8 flex items-center justify-center mr-2 ${
              message.sender === 'human' ? 'bg-blue-100 text-blue-700' : 'bg-purple-100 text-purple-700'
            }`}>
              {message.sender === 'human' ? (
                <User className="h-5 w-5" />
              ) : (
                <Bot className="h-5 w-5" />
              )}
            </div>
            <h2 className="text-lg font-medium">
              {message.sender_label} - 消息 #{message.index + 1}
            </h2>
            
            {/* 分支标记 */}
            {message.is_branch_point && (
              <span className="ml-2 px-2 py-0.5 bg-purple-100 text-purple-700 text-xs rounded-full">
                分支点
              </span>
            )}
            {message.branch_level > 0 && (
              <span className="ml-2 px-2 py-0.5 bg-indigo-100 text-indigo-700 text-xs rounded-full">
                分支 {message.branch_level}
              </span>
            )}
          </div>
          <div className="text-sm text-gray-500 mb-2">
            {message.timestamp}
          </div>
        </div>
        
        {/* 根据活动标签显示不同内容 */}
        {activeTab === 'content' && (
          <div 
            ref={messageContentRef}
            className="prose max-w-none pb-4 whitespace-pre-wrap content-container"
          >
            {searchQuery ? highlightSearchText(message.display_text) : message.display_text}
          </div>
        )}
        
        {activeTab === 'thinking' && message.thinking && (
          <div className="bg-gray-50 border border-gray-200 rounded-md p-4 mt-2">
            <div className="flex items-center mb-2">
              <AlertTriangle className="h-5 w-5 text-amber-500 mr-2" />
              <h3 className="font-medium">Claude 的思考过程</h3>
            </div>
            <pre className="whitespace-pre-wrap text-sm bg-gray-100 p-4 rounded overflow-auto max-h-[600px]">
              {message.thinking}
            </pre>
          </div>
        )}
        
        {activeTab === 'artifacts' && artifacts.length > 0 && (
          <div className="space-y-4">
            {artifacts.map((artifact, idx) => (
              <div key={idx} className="border border-blue-200 rounded-md overflow-hidden">
                <div className="bg-blue-50 p-3 border-b border-blue-200">
                  <div className="flex items-center justify-between">
                    <h3 className="font-medium text-blue-700 flex items-center">
                      <FileText className="h-4 w-4 mr-2" />
                      {artifact.title || `Artifact ${idx + 1}`}
                    </h3>
                    <div className="text-xs bg-blue-100 px-2 py-0.5 rounded text-blue-700">
                      {artifact.type}
                    </div>
                  </div>
                  {artifact.id && (
                    <div className="text-xs text-blue-600 mt-1">
                      ID: {artifact.id}
                    </div>
                  )}
                </div>
                
                <div className="p-4">
                  {artifact.command === 'create' && (
                    <>
                      {artifact.language ? (
                        renderCode(artifact.content, artifact.language)
                      ) : (
                        <div className="whitespace-pre-wrap bg-gray-50 p-3 rounded border border-gray-200 text-sm">
                          {artifact.content}
                        </div>
                      )}
                    </>
                  )}
                  
                  {(artifact.command === 'update' || artifact.command === 'rewrite') && (
                    <div className="space-y-3">
                      <div>
                        <div className="font-medium text-sm mb-1">原始内容:</div>
                        <div className="whitespace-pre-wrap bg-gray-50 p-3 rounded border border-gray-200 text-sm">
                          {artifact.old_str}
                        </div>
                      </div>
                      <div>
                        <div className="font-medium text-sm mb-1">新内容:</div>
                        <div className="whitespace-pre-wrap bg-green-50 p-3 rounded border border-green-200 text-sm">
                          {artifact.new_str}
                        </div>
                      </div>
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
        
        {activeTab === 'tools' && tools.length > 0 && (
          <div className="space-y-4">
            {tools.map((tool, idx) => (
              <div key={idx} className="border border-gray-200 rounded-md overflow-hidden">
                <div className="bg-gray-50 p-3 border-b border-gray-200">
                  <h3 className="font-medium flex items-center">
                    <Wrench className="h-4 w-4 mr-2 text-gray-600" />
                    {tool.name}
                  </h3>
                </div>
                
                <div className="p-4">
                  {/* 输入参数 */}
                  {tool.input && typeof tool.input === 'object' && (
                    <div className="mb-4">
                      <h4 className="text-sm font-medium mb-2">输入参数:</h4>
                      <pre className="bg-gray-100 p-3 rounded text-xs overflow-auto">
                        {JSON.stringify(tool.input, null, 2)}
                      </pre>
                    </div>
                  )}
                  
                  {/* 工具结果 */}
                  {tool.result && (
                    <div>
                      <h4 className="text-sm font-medium mb-2">结果:</h4>
                      {tool.result.is_error ? (
                        <div className="bg-red-50 text-red-700 p-3 rounded border border-red-200">
                          <AlertTriangle className="h-4 w-4 inline-block mr-1" />
                          执行出错
                        </div>
                      ) : (
                        <div className="bg-gray-50 p-3 rounded border border-gray-200">
                          {renderToolResultContent(tool.result.content)}
                        </div>
                      )}
                    </div>
                  )}
                </div>
              </div>
            ))}
          </div>
        )}
        
        {activeTab === 'citations' && citations.length > 0 && (
          <div className="space-y-4">
            <h3 className="font-medium flex items-center">
              <Book className="h-5 w-5 mr-2 text-blue-600" />
              引用来源
            </h3>
            
            <div className="grid gap-3">
              {citations.map((citation, idx) => (
                <div key={idx} className="border border-gray-200 rounded-md p-3 hover:bg-gray-50">
                  <h4 className="font-medium mb-1">
                    <a 
                      href={citation.url || "#"} 
                      target="_blank" 
                      rel="noopener noreferrer"
                      className="text-blue-600 hover:underline"
                    >
                      {citation.title || "未知来源"}
                    </a>
                  </h4>
                  <div className="text-sm text-gray-500">
                    {citation.url || "无URL"}
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}
        
        {activeTab === 'branch' && (
          <div className="space-y-4">
            <h3 className="font-medium flex items-center">
              <GitBranch className="h-5 w-5 mr-2 text-purple-600" />
              分支信息
            </h3>
            
            <div className="bg-gray-50 border border-gray-200 rounded-md p-4">
              <table className="w-full text-sm">
                <tbody>
                  {message.is_branch_point && (
                    <tr>
                      <td className="py-2 font-medium pr-4">分支点:</td>
                      <td className="py-2">
                        <span className="bg-purple-100 text-purple-700 px-2 py-0.5 rounded-full">
                          是
                        </span>
                      </td>
                    </tr>
                  )}
                  {message.branch_id !== null && (
                    <tr>
                      <td className="py-2 font-medium pr-4">分支ID:</td>
                      <td className="py-2">{message.branch_id}</td>
                    </tr>
                  )}
                  {message.branch_level > 0 && (
                    <tr>
                      <td className="py-2 font-medium pr-4">分支级别:</td>
                      <td className="py-2">{message.branch_level}</td>
                    </tr>
                  )}
                  <tr>
                    <td className="py-2 font-medium pr-4">消息UUID:</td>
                    <td className="py-2 text-gray-600">{message.uuid || '无'}</td>
                  </tr>
                  <tr>
                    <td className="py-2 font-medium pr-4">父消息UUID:</td>
                    <td className="py-2 text-gray-600">{message.parent_uuid || '无'}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        )}
      </div>
    </div>
  );
});

export default MessageDetail;