import React, { useState, useEffect, useCallback, useRef } from 'react';
import { Search, Trash2, Loader, ChevronUp, ChevronDown, ArrowUp, ArrowDown } from 'lucide-react';

const MessageList = ({ 
  messages, 
  selectedIndices, 
  onMessageSelect, 
  markedItems,
  importantItems,
  deletedItems,
  searchQuery,
  setSearchQuery,
  contentSearchQuery,  // 新增: 内容搜索关键词
  setContentSearchQuery, // 新增: 设置内容搜索关键词的函数
  showCompleted,
  showDeleted,
  handleMarkDeleted,
  isSearching,
  showSearchPanel
}) => {
  // 搜索状态
  const [localSearchResults, setLocalSearchResults] = useState([]);
  const [currentLocalSearchIndex, setCurrentLocalSearchIndex] = useState(-1);
  const [totalSearchCount, setTotalSearchCount] = useState(0);
  const [currentSearchPosition, setCurrentSearchPosition] = useState(0);
  const [hasSearchResults, setHasSearchResults] = useState(false);
  
  // 新增: 内容搜索状态
  const [contentSearchCount, setContentSearchCount] = useState(0);
  const [currentContentPosition, setCurrentContentPosition] = useState(0);
  
  // 搜索输入框引用
  const searchInputRef = useRef(null);
  const contentSearchInputRef = useRef(null);
  
  // 初始化本地搜索结果
  useEffect(() => {
    if (searchQuery.trim()) {
      const results = messages
        .filter(msg => msg.display_text && msg.display_text.toLowerCase().includes(searchQuery.toLowerCase()))
        .map(msg => msg.index);
        
      setLocalSearchResults(results);
    } else {
      setLocalSearchResults([]);
      setCurrentLocalSearchIndex(-1);
    }
  }, [messages, searchQuery]);
  
  // 处理搜索状态更新事件
  useEffect(() => {
    const handleSearchUpdate = (e) => {
      const { totalCount, displayIndex, query, currentResult } = e.detail;
      
      // 只处理当前搜索查询的更新
      if (query !== searchQuery) return;
      
      setTotalSearchCount(totalCount);
      setCurrentSearchPosition(displayIndex);
      setHasSearchResults(totalCount > 0);
      
      // 如果当前结果在本文件中，更新高亮
      if (currentResult && localSearchResults.includes(currentResult.messageIndex)) {
        const localIndex = localSearchResults.indexOf(currentResult.messageIndex);
        setCurrentLocalSearchIndex(localIndex);
      } else {
        // 如果结果不在当前文件，重置高亮索引
        setCurrentLocalSearchIndex(-1);
      }
    };
    
    window.addEventListener('search-status-update', handleSearchUpdate);
    return () => window.removeEventListener('search-status-update', handleSearchUpdate);
  }, [searchQuery, localSearchResults]);
  
  // 新增: 处理内容搜索更新事件
  useEffect(() => {
    const handleContentSearchUpdate = (e) => {
      const { count, position } = e.detail;
      
      setContentSearchCount(count);
      setCurrentContentPosition(position);
    };
    
    window.addEventListener('content-search-update', handleContentSearchUpdate);
    return () => window.removeEventListener('content-search-update', handleContentSearchUpdate);
  }, []);
  
  // 同步搜索状态
  useEffect(() => {
    if (window.searchManager && searchQuery.trim()) {
      const state = window.searchManager.getState();
      if (state.totalCount > 0) {
        setTotalSearchCount(state.totalCount);
        setCurrentSearchPosition(state.displayIndex);
        setHasSearchResults(true);
      }
    }
  }, [searchQuery]);
  
  // 发送搜索导航请求
  const sendSearchNavigationRequest = useCallback((action, data = {}) => {
    const event = new CustomEvent('search-navigation', {
      detail: { action, ...data }
    });
    window.dispatchEvent(event);
  }, []);
  
  // 搜索相关操作
  const handleSearchSubmit = useCallback((e) => {
    if (e) e.preventDefault();
    
    if (!searchQuery.trim()) return;
    
    // 如果本地有结果，先跳转到第一个
    if (localSearchResults.length > 0) {
      onMessageSelect(localSearchResults[0], false);
      setCurrentLocalSearchIndex(0);
      
      // 通知搜索管理器
      sendSearchNavigationRequest('search', { query: searchQuery });
    } else {
      // 如果本地没有结果，打开搜索面板
      sendSearchNavigationRequest('open-panel');
    }
  }, [searchQuery, localSearchResults, onMessageSelect, sendSearchNavigationRequest]);
  
  // 处理搜索输入框按回车
  const handleSearchKeyPress = useCallback((e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      handleSearchSubmit();
    }
  }, [handleSearchSubmit]);
  
  // 查找上一个结果
  const handleSearchPrev = useCallback(() => {
    sendSearchNavigationRequest('prev');
  }, [sendSearchNavigationRequest]);
  
  // 查找下一个结果
  const handleSearchNext = useCallback(() => {
    sendSearchNavigationRequest('next');
  }, [sendSearchNavigationRequest]);
  
  // 新增: 内容搜索提交
  const handleContentSearchSubmit = useCallback(() => {
    const input = contentSearchInputRef.current;
    if (input && input.value.trim()) {
      setContentSearchQuery(input.value.trim());
      
      // 发送内容搜索事件
      const event = new CustomEvent('content-search', {
        detail: { 
          query: input.value.trim(),
          position: 0 // 从第一个结果开始
        }
      });
      window.dispatchEvent(event);
    } else {
      setContentSearchQuery('');
    }
  }, [setContentSearchQuery]);
  
  // 新增: 内容搜索按键处理
  const handleContentSearchKeyPress = useCallback((e) => {
    if (e.key === 'Enter') {
      e.preventDefault();
      handleContentSearchSubmit();
    }
  }, [handleContentSearchSubmit]);
  
  // 新增: 上一个内容匹配
  const handleContentSearchPrev = useCallback(() => {
    if (!contentSearchQuery) return;
    
    // 发送内容搜索导航事件
    const event = new CustomEvent('content-search-navigation', {
      detail: { action: 'prev' }
    });
    window.dispatchEvent(event);
  }, [contentSearchQuery]);
  
  // 新增: 下一个内容匹配
  const handleContentSearchNext = useCallback(() => {
    if (!contentSearchQuery) return;
    
    // 发送内容搜索导航事件
    const event = new CustomEvent('content-search-navigation', {
      detail: { action: 'next' }
    });
    window.dispatchEvent(event);
  }, [contentSearchQuery]);
  
  // 处理消息点击
  const handleMessageClick = useCallback((index, ctrlKey) => {
    setTimeout(() => {
      onMessageSelect(index, ctrlKey);
    }, 0);
  }, [onMessageSelect]);

  return (
    <div className="flex flex-col h-full overflow-hidden">
      {/* 全局搜索区域 */}
      <div className="p-2 border-b border-gray-300 bg-white">
        <div className="flex space-x-2 items-center mb-2">
          <div className="relative flex-1">
            <input 
              id="global-search-input"
              ref={searchInputRef}
              type="text" 
              placeholder="搜索全部消息..." 
              className="w-full pl-8 pr-2 py-1 border border-gray-300 rounded text-sm"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              onKeyPress={handleSearchKeyPress}
            />
            <Search
              className="absolute left-2 top-1.5 h-4 w-4 text-gray-400 cursor-pointer" 
              onClick={handleSearchSubmit}
              title="搜索消息"
            />
            {isSearching && (
              <Loader className="absolute right-2 top-1.5 h-4 w-4 text-blue-500 animate-spin" />
            )}
          </div>
          
          {/* 导航按钮 */}
          <button 
            className="bg-gray-200 text-gray-600 py-1 px-1 rounded-l text-sm hover:bg-gray-300 disabled:opacity-50"
            onClick={handleSearchPrev}
            title="上一个结果"
            disabled={!hasSearchResults}
          >
            <ChevronUp className="h-4 w-4" />
          </button>
          
          <button 
            className="bg-gray-200 text-gray-600 py-1 px-1 rounded-r text-sm hover:bg-gray-300 disabled:opacity-50"
            onClick={handleSearchNext}
            title="下一个结果"
            disabled={!hasSearchResults}
          >
            <ChevronDown className="h-4 w-4" />
          </button>
          
          <button 
            className="bg-blue-500 text-white py-1 px-2 rounded text-sm whitespace-nowrap hover:bg-blue-600 flex items-center"
            onClick={showSearchPanel}
            title="打开全局搜索"
          >
            <Search className="h-3.5 w-3.5 mr-1" />
            搜索
          </button>
        </div>
        
        {/* 新增: 内容搜索输入区 */}
        <div className="flex space-x-2 items-center">
          <div className="relative flex-1">
            <input 
              id="content-search-input"
              ref={contentSearchInputRef}
              type="text" 
              placeholder="搜索消息内容..." 
              className="w-full pl-8 pr-2 py-1 border border-gray-300 rounded text-sm content-search-input"
              defaultValue={contentSearchQuery}
              onKeyPress={handleContentSearchKeyPress}
            />
            <Search
              className="absolute left-2 top-1.5 h-4 w-4 text-gray-400 cursor-pointer" 
              onClick={handleContentSearchSubmit}
              title="搜索内容"
            />
          </div>
          
          {/* 内容导航按钮 */}
          <button 
            className="content-search-btn py-1 px-1 rounded-l text-sm disabled:opacity-50"
            onClick={handleContentSearchPrev}
            title="上一个内容匹配"
            disabled={!contentSearchQuery || contentSearchCount === 0}
          >
            <ArrowUp className="h-4 w-4" />
          </button>
          
          <button 
            className="content-search-btn py-1 px-1 rounded-r text-sm disabled:opacity-50"
            onClick={handleContentSearchNext}
            title="下一个内容匹配"
            disabled={!contentSearchQuery || contentSearchCount === 0}
          >
            <ArrowDown className="h-4 w-4" />
          </button>
        </div>
        
        {/* 搜索结果信息 */}
        <div className="text-xs text-gray-600 mt-1 flex justify-between">
          <div>
            {searchQuery.trim() && (
              <>
                {hasSearchResults ? (
                  <>
                    搜索到 {totalSearchCount} 个结果 
                    {currentSearchPosition > 0 && (
                      <span className="ml-2">当前: 第 {currentSearchPosition} 个</span>
                    )}
                  </>
                ) : (
                  <>未找到匹配内容</>
                )}
              </>
            )}
          </div>
          
          {/* 内容搜索信息 */}
          <div>
            {contentSearchQuery && (
              <>
                {contentSearchCount > 0 ? (
                  <span className="text-green-600">
                    内容匹配: {currentContentPosition + 1}/{contentSearchCount}
                  </span>
                ) : (
                  <span className="text-red-500">内容中未找到匹配</span>
                )}
              </>
            )}
          </div>
        </div>
      </div>
      
      {/* 消息列表表头 */}
      <div className="bg-gray-100 border-b border-gray-300 p-1 sticky top-0 z-10">
        <div className="grid grid-cols-12 text-sm font-medium text-gray-600">
          <div className="col-span-1 px-3">#</div>
          <div className="col-span-2 px-1">发送者</div>
          <div className="col-span-2 px-1">时间</div>
          <div className="col-span-2 px-1">分支</div>
          <div className="col-span-2 px-1">标记</div>
          <div className="col-span-2 px-1 flex justify-end pr-2">
            <button
              className="text-gray-500 hover:text-red-600 focus:outline-none"
              onClick={handleMarkDeleted}
              disabled={selectedIndices.length === 0}
              title="移除选中消息"
            >
              <Trash2 className="h-4 w-4" />
            </button>
          </div>
        </div>
      </div>
      
      {/* 消息列表 - 可滚动区域 */}
      <div className="flex-1 overflow-y-auto border-gray-200">
        {messages?.map(msg => {
          // 如果消息被标记为已删除且设置为隐藏已删除，则跳过
          if (deletedItems.has(msg.index) && !showDeleted) {
            return null;
          }
          
          // 如果消息被标记为已完成且设置为隐藏已完成，则跳过
          if (markedItems.has(msg.index) && !showCompleted) {
            return null;
          }
          
          // 分支信息
          let branchInfo = "";
          if (msg.is_branch_point) {
            branchInfo = "🔀";
          } else if (msg.branch_level > 0) {
            branchInfo = `${msg.branch_level}`;
          }
          
          // 标记信息
          let markInfo = null;
          
          if (deletedItems.has(msg.index)) {
            markInfo = <span className="deleted-mark" style={{color: selectedIndices.includes(msg.index) ? '#ffcdd2' : '#f44336'}}>🗑️</span>;
          } else if (markedItems.has(msg.index) && importantItems.has(msg.index)) {
            markInfo = (
              <div className="dual-mark" style={{color: selectedIndices.includes(msg.index) ? 'white' : 'inherit'}}>
                <span className="completed-mark" style={{color: selectedIndices.includes(msg.index) ? '#a5d6a7' : '#4caf50'}}>✓</span>
                <span className="important-mark" style={{color: selectedIndices.includes(msg.index) ? '#ffe082' : '#ff8f00'}}>⭐</span>
              </div>
            );
          } else if (markedItems.has(msg.index)) {
            markInfo = <span className="completed-mark" style={{color: selectedIndices.includes(msg.index) ? '#a5d6a7' : '#4caf50'}}>✓</span>;
          } else if (importantItems.has(msg.index)) {
            markInfo = <span className="important-mark" style={{color: selectedIndices.includes(msg.index) ? '#ffe082' : '#ff8f00'}}>⭐</span>;
          }

          // 已删除消息的样式
          const isDeleted = deletedItems.has(msg.index);
          const deletedStyle = isDeleted ? { textDecoration: 'line-through', opacity: 0.6 } : {};

          const grid = "grid grid-cols-12 px-1 py-3 text-sm";
          const messagePreviewClass = selectedIndices.includes(msg.index) 
            ? "col-span-4 px-1 pl-3 truncate text-white" 
            : "col-span-4 px-1 pl-3 truncate text-gray-700";
          
          // 检查是否匹配搜索条件
          const isMatch = searchQuery && 
                         msg.display_text && 
                         msg.display_text.toLowerCase().includes(searchQuery.toLowerCase());
          
          // 检查是否是当前选中的搜索结果
          const isCurrentSearchResult = isMatch && localSearchResults.indexOf(msg.index) === currentLocalSearchIndex;
          
          // 检查是否含有内容搜索关键词
          const hasContentMatch = contentSearchQuery && 
                                 msg.display_text && 
                                 msg.display_text.toLowerCase().includes(contentSearchQuery.toLowerCase());
          
          return (
            <div 
              key={msg.index} 
              className={`border-b border-gray-200 hover:bg-gray-100 ${
                msg.sender === 'human' ? 'human-msg' : 'claude-msg'
              } message-list-item cursor-pointer ${isMatch ? 'search-match' : ''} ${
                isCurrentSearchResult ? 'current-search-result' : ''
              } ${hasContentMatch ? 'content-match' : ''}`}
              onClick={(e) => handleMessageClick(msg.index, e.ctrlKey)}
              style={{
                backgroundColor: selectedIndices.includes(msg.index) 
                  ? '#5c6bc0' 
                  : isCurrentSearchResult 
                    ? 'rgba(255, 241, 118, 0.5)' // 当前搜索结果使用更亮的黄色
                    : isMatch 
                      ? 'rgba(255, 241, 118, 0.3)' 
                      : hasContentMatch && !selectedIndices.includes(msg.index)
                        ? 'rgba(200, 230, 201, 0.4)' // 含有内容匹配的消息使用淡绿色
                        : '',
                color: selectedIndices.includes(msg.index) ? 'white' : '',
                ...deletedStyle
              }}
            >
              <div className={grid}>
                <div className="col-span-1 px-1 text-center">{msg.index + 1}</div>
                <div className="col-span-2 px-1 font-medium">
                  {msg.sender_label}
                </div>
                <div className="col-span-3 px-1 text-xs" style={{color: selectedIndices.includes(msg.index) ? '#e0e0e0' : '#6b7280'}}>
                  {msg.timestamp}
                </div>
                <div className="col-span-1 px-1 text-center" style={{color: selectedIndices.includes(msg.index) ? '#e0e0e0' : '#9c27b0'}}>
                  {branchInfo}
                </div>
                <div className="col-span-1 px-1 text-center mark-container">
                  {markInfo}
                </div>
                <div className={messagePreviewClass}>
                  {contentSearchQuery && msg.display_text ? (
                    // 预览中高亮显示内容搜索关键词
                    highlightPreview(msg.display_text.substring(0, 100), contentSearchQuery)
                  ) : (
                    msg.display_text?.substring(0, 100)
                  )}
                </div>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
};

// 高亮预览文本中的关键词
const highlightPreview = (text, query) => {
  if (!text || !query) return text;
  
  const parts = text.split(new RegExp(`(${query})`, 'gi'));
  return (
    <>
      {parts.map((part, i) => (
        part.toLowerCase() === query.toLowerCase() ? (
          <span key={i} className="content-match-text">{part}</span>
        ) : (
          <span key={i}>{part}</span>
        )
      ))}
    </>
  );
};

export default MessageList;